import * as React from "react"
import Layout from "../components/layout"
import { graphql } from "gatsby"

import PageTitle from "../components/PageTitle"

const PageTemplate = ({ data }) => {
  let title = data.privacidad.nodes[0].content.title
  let string_content = ""
  data.content.nodes.forEach(item => {
    string_content = string_content + item.content
  })
  return (
    <Layout>
      <PageTitle title={title} />
      <section className="construct-service-area  privacy-section section_t_70 section_b_100">

        <div
          className="container"
          dangerouslySetInnerHTML={{ __html: string_content }}
        ></div>

      </section>
    </Layout>
  )
}

export const query = graphql`
  {
    content: allPoliticaPrivacidadJson {
      nodes {
        content
      }
    }

    privacidad: allPagesJson(
      filter: { idPage: { eq: "politica-privacidad" } }
    ) {
      nodes {
        idPage
        content {
          category
          description
          title
        }
      }
    }
  }
`

export default PageTemplate
